@use "../abstracts" as *;

/* ? !== <==>Footer<==> !== */
.footer {
   min-height: 100vh;
   max-height: 100vh;
   overflow: hidden;

   .container {
      @include flexCenter();

      height: 200px;
      padding: 20px;
   }

   .second-container {
      @include flexCenter();

      width: 95%;
      height: 50px;
      border-radius: 8px;
      border: 2px solid $secondColor;
   }

   // * Button Dropup
   .dropup {
      width: 15%;
      height: 100%;
      position: relative;
      display: inline-block;
   }

   .dropbtn {
      width: 100%;
      height: 100%;
      color: $primaryColor;
      font-size: 16px;
      font-weight: bold;
      background-color: $secondColor;
      // padding: 16px;
      border: none;
      border-radius: 5px 0 0 5px;

      img {
         width: 25px;
         height: 25px;
      }
   }

   .dropup-content {
      display: none;
      position: absolute;
      bottom: 45px;

      min-width: 160px;
      background-color: $secondColor;
      border-radius: 8px;
      box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
      z-index: 1;
   }

   .dropup-content button {
      @include flexCenter();
      flex-wrap: wrap;

      width: 100%;
      color: black;
      text-decoration: none;
      font-weight: 600;
      background-color: $secondColor;
      border: none;
      padding: 12px 16px;
      transition: 0.3s;

      > i {
         width: 15%;
         height: 100%;
         font-size: 20px;
         transition: 0.3s;
      }

      p {
         width: 85%;
         height: 100%;
         margin: 0;
      }
   }

   .site-link {
      i {
         font-size: 20px;
      }
   }

   .dropup-content button:active {
      letter-spacing: 2px;
   }

   .dropup-content button:hover {
      color: $secondColor;
      letter-spacing: 1.1px;
      background-color: $primaryColor;
      padding: 12px 12px;
      border-radius: 8px;
   }

   .dropup:hover .dropup-content {
      display: block;
      border: 2px solid $secondColor;
   }

   .dropup:hover .dropbtn {
      background-color: darken($secondColor, 10%);
   }

   .link {
      @include flexCenter();

      width: 85%;
      height: 100%;
      justify-content: start;

      p {
         width: 72%;
         height: 100%;
         margin: 0;
         padding-left: 10px;
         padding-top: 10px;
         margin-right: 5%;
         overflow: auto;
      }

      p::-webkit-scrollbar {
         display: none;
      }

      .copy,
      .site-link {
         @include flexCenter();

         width: 15%;
         height: 100%;
      }

      .copy {
         border-right: 2px solid $secondColor;
         border-left: 2px solid $secondColor;
      }

      .copy {
         color: $primaryColor;
         background-color: $secondColor;
         transition: 0.2s;

         svg {
            width: 20px;
            height: 20px;
            color: $primaryColor;
            transition: 0.2s;
         }

         &:active {
            background-color: $primaryColor;
         }

         &:active svg {
            color: $secondColor;
         }
      }

      .site-link {
         color: $secondColor;

         &:active {
            color: $primaryColor;
            background-color: $secondColor;
         }
      }
   }
}
